import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import H from 'history';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles({
  root: {
    paddingTop: 144,
    paddingLeft: 88,
  },
  title: {
    color: '#FFFFFF',
    fontSize: 40,
    fontWeight: 'lighter',
  },
  subTitle: {
    color: '#A8D574',
    fontSize: 18,
    paddingBottom: 40,
  },
  bankButton: {
    fontFamily: 'system-ui, -apple-system, "Roboto", "Helvetica", "Arial", sans-serif',
    fontSize: 24,
    borderWidth: 2,
    borderRadius: 0,
    '&:not(.MuiButton-outlinedPrimary)': {
      color: '#707070',
      borderColor: '#707070',
    },
  },
  bankHelperText: {
    paddingTop: 40,
    color: '#FFD065',
    fontSize: 14,
    lineHeight: '32px',
  },
  buttonContainer: {
    paddingTop: 56,
    textAlign: 'center',
  },
  button: {
    fontFamily: 'system-ui, -apple-system, "Roboto", "Helvetica", "Arial", sans-serif',
    fontSize: 18,
    fontWeight: 'bold',
    width: 257,
  },
});

interface PropsI {
  history: H.History;
}

function PaymentAtm(props: PropsI): JSX.Element {
  const classes = useStyles();

  const [selectedBank, setSelectedBank] = useState('');

  const handleFormSubmit = (): void => {
    if (selectedBank) {
      props.history.push('/successful/payment/creditCard');
    }
  };

  return (
    <div className={classes.root}>
      <h3 className={classes.title}> ATM</h3>

      <div className={classes.subTitle}></div>

      <Grid container spacing={2}>
        {[
          '', '', '', '',
          '', '', '', '',
          '', '', '',
        ].map((bank): JSX.Element => (
          <Grid key={bank} item xs={12} sm={6}>
            <Button
              onClick={(): void => setSelectedBank(bank)}
              className={classes.bankButton}
              variant="outlined"
              color={selectedBank === bank ? 'primary' : 'default'}
              fullWidth
            >{bank}</Button>
          </Grid>
        ))}
      </Grid>

      <div className={classes.bankHelperText}>
        1. WebATM0WebATM15
        <br />
        2. 15
        <br />
        WebATM0 WebATM15
      </div>

      <div className={classes.buttonContainer}>
        <Button
          variant="contained"
          color="primary"
          className={classes.button}
          onClick={handleFormSubmit}
        >
          
        </Button>
      </div>
    </div>
  );
}

export default withRouter(PaymentAtm);
